import React, { useEffect, useState } from 'react';
import './medical-list-page.css';
import { Typography } from '@material-ui/core';
import { Link } from 'react-router-dom';
import { httpFetch } from '../../../services/http-fetch';
import { baseUrl } from '../../../services/base-url';
import { onlyLocalDate } from '../../../utils/date';
import { InspectionsList } from '../../../types/inspection';
import Spinner from '../common/spinner/spinner';
import NavigationHeader from '../common/navigation-header/navigation-header';
import Lists from '../common/lists/lists';
import ListItems from '../common/lists/list-items/list-items';
import ListItem from '../common/lists/list-item/list-item';
import Delimiter from '../common/delimiter/delimiter';

const MedicalListPage = () => {
  const [checkupsList, setCheckupsList] = useState<any>([]);
  const [value, setValue] = useState(10);
  const [load, setLoad] = useState(false);

  const getCheckupsList = async () => {
    const res = await httpFetch.get(`${baseUrl}/checkups?page[size]=${value}&sort=-created_at`);
    if (res && res.data) {
      setCheckupsList(res.data);
      setLoad(false);
    }
  };

  const handleScroll = () => {
    const el = document.getElementById('medlist');
    if (el) {
      const scrollTop = el.scrollTop + 10;
      const scrollBottom = el.scrollHeight - el.offsetHeight;
      if (scrollTop >= scrollBottom) {
        setValue(value + 10);
        setLoad(true);
        getCheckupsList();
      }
    }
  };

  useEffect(() => {
    getCheckupsList();
    setValue(value + 5);
  }, []);

  if (checkupsList.length === 0) {
    return <Spinner />;
  }

  return (
    <div id="medlist" className="medical-list-page" onScroll={handleScroll}>
      <NavigationHeader>Список медосмотров</NavigationHeader>
      <Lists>
        {checkupsList.map((item: { id: number; attributes: any }) => {
          const { datetime_start }: InspectionsList = item.attributes;
          return (
            <Link key={item.id} to={`/inspection/${item.id}`} className="medical-list-link">
              <ListItems>
                <ListItem>
                  <label>Номер</label> <Typography>{item.id}</Typography>
                </ListItem>
                <ListItem>
                  <label>Дата выдачи</label>
                  <Typography>{onlyLocalDate(datetime_start)}</Typography>
                </ListItem>
              </ListItems>
              <Delimiter />
            </Link>
          );
        })}
      </Lists>
      {load && <Spinner />}
    </div>
  );
};

export default MedicalListPage;
