export const dateToLocalTime = (time = '') => {
  const x = new Date();
  const currentTimeZoneOffsetInHours = (x.getTimezoneOffset() / 60) * -1;
  Date.prototype.addHours = function (h) {
    this.setHours(this.getHours() + h);
    return this;
  };
  return `
  ${new Date(time).addHours(currentTimeZoneOffsetInHours).toISOString().split('T')[0].split('.')[0]}
  ${
    new Date(time).addHours(currentTimeZoneOffsetInHours).toISOString().split('T')[1].split('.')[0]
  }`;
};

